.nav {
    grid-area: n;
    background-color: #131c21;
    padding: 20px;
    height: 100vh;
    border-right: 1px solid #b1b3b5;
}

.nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.nav li {
    margin-bottom: 20px;
}

.nav a {
    color: #fff;
    font-size: 18px;
    letter-spacing: 1px;
    text-decoration: none;
}

.nav a:hover {
    color: #af8a31;
    border-color: #af8a31;
    transition-duration: 500ms;
}

.nav a.active {
    color: gold;
}