.header {
    grid-area: h;
    background-color: #0b172a;
    display: flex;
    padding: 10px 20px;
    border-bottom: 1px solid #b1b3b5;
}

.loginBlock {
    float: right;
    color: #fff;
    font-size: 18px;
    margin: auto 0 auto auto;
}

.logOut,
.logIn {
    background-color: transparent;
    border: 1px solid #b1b3b5;
    color: #fff;
    padding: 5px 10px;
    font-size: 18px;
    cursor: pointer;
}

.logOut:hover,
.logIn:hover {
    color: #af8a31;
    border-color: #af8a31;
    transition-duration: 500ms;
}

.logIn {
    text-decoration: none;
}

.logOut {
    font-family: 'IBMPlexSerif', serif;
}